<template>
  <div class="d-flex">
    <navigation />
    <div class="row m-0 flex-fill" style="overflow: hidden;">
      <div class="col-3 p-2">
        <h4 class="my-2 ml-2">Settings</h4>
        <div class="nav flex-column nav-pills">
          <!-- <a
            class="nav-link"
            :class="active('sources')"
            @click="updateSelected('sources')"
          >
            <i class="la la-facebook-square"></i>
            <span>Sources</span>
          </a> -->
          <a
            v-if="showChannel && admin.role=='Admin'"
            class="nav-link"
            :class="active('channels')"
            @click="updateSelected('channels')"
          >
            <i class="la la-project-diagram"></i>
            <span>Channels</span>
          </a>
          <a
            v-if="showStep && admin.role == 'Admin'"
            class="nav-link"
            :class="active('steps')"
            @click="updateSelected('steps')"
          >
            <i class="las la-tasks"></i>
            <span>Customer Journey</span>
          </a>
          <a
            class="nav-link"
            :class="active('answers')"
            @click="updateSelected('answers')"
          >
            <i class="la la-comment-dots"></i>
            <span>Pre-Answer Sets</span>
          </a>
          <a
            class="nav-link"
            :class="active('users')"
            @click="updateSelected('users')"
          >
            <i class="la la-user"></i>
            <span>Users</span>
          </a>
          <a
            class="nav-link"
            :class="active('tags')"
            @click="updateSelected('tags')"
          >
            <i class="las la-tags"></i>
            <span>Tags</span>
          </a>
          <a
            class="nav-link"
            :class="active('products')"
            @click="updateSelected('products')"
          >
            <i class="la la-ticket-alt"></i>
            <span>Products</span>
          </a>
        </div>
      </div>
      <sources v-if="selected == 'sources'" />
      <channel v-else-if="selected == 'channels'" />
      <answer-set 
        v-else-if="selected == 'answers'" 
        :showChannel="showChannel" 
        :showStep="showStep"/>
      <customer-journey v-else-if="selected == 'steps'" />
      <user  
        v-else-if="selected == 'users'" 
        :showChannel="showChannel" 
        :showStep="showStep"/>
      <product v-else-if="selected == 'products'" />
      <tag v-else-if="selected == 'tags'" />
    </div>
  </div>
</template>
<script>
import Navigation from "@/components/Navigation";
import axios from 'axios';
import "bootstrap/scss/bootstrap.scss";
import "simplebar/dist/simplebar.min.css";
import { mapGetters } from 'vuex';

export default {
  components: {
    Navigation,
    "customer-journey": () => import("./CustomerJourney"),
    sources: () => import("./Source"),
    channel: () => import("./Channel"),
    user: () => import("./User"),
    product: () => import("./Product"),
    tag: () => import("./Tag"),
    "answer-set": () => import("./AnswerSet")
  },
  data() {
    return {
      selected: "",
      showChannel: false,
      showStep: false,
    };
  },
  computed:{
    ...mapGetters(['admin'])
  },
  methods: {
    active(type) {
      return this.selected == type ? "active" : "";
    },
    updateSelected(type) {
      this.selected = type;
    }
  },
  mounted(){
    axios.get("admin/settings")
      .then(res => {
        if(res.status == 200){
          this.showChannel = res.data.channel;
          this.showStep = res.data.step;
        }
      });
  }
};
</script>
<style lang="scss" scoped>
@import "~bootstrap/scss/bootstrap.scss";
.nav-link {
  cursor: pointer;
  text-align: left;
  transition: all 0.1s linear;
  padding: .8em 1em; 
  &:hover {
    color: $primary !important;
  }
  &.active {
    border-radius: 0.65rem;
    &:hover {
      color: #fff !important;
    }
  }
  i {
    font-size: 1.5em;
    position: absolute;
  }
  span {
    margin-left: 40px;
  }
}
</style>
